/* Stylish Gen Z Vibes for the Trip Details Page */
.trip-details-page {
    font-family: 'Poppins', sans-serif;
    margin: 20px;
    padding: 20px;
    background: linear-gradient(to right, #f5f7fa, #c3cfe2);
    color: #333;
  }
  
  /* Media Gallery */
  .media-gallery {
    text-align: center;
    margin-bottom: 40px;
  }
  
  .media-gallery h2 {
    font-size: 28px;
    color: #444;
    font-weight: 700;
    margin-bottom: 20px;
    text-transform: uppercase;
    letter-spacing: 1.5px;
  }
  
  .gallery {
    display: flex;
    gap: 20px;
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .gallery img,
  .gallery video {
    width: 280px;
    height: 200px;
    object-fit: cover;
    border-radius: 15px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .gallery img:hover,
  .gallery video:hover {
    transform: scale(1.05);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
  }
  
  /* Details Section */
  .details-sections {
    display: flex;
    justify-content: space-between;
    gap: 30px;
    margin-top: 50px;
  }
  
  .trip-plan,
  .trip-map {
    width: 48%;
    background: #ffffff;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.15);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .trip-plan:hover,
  .trip-map:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
  }
  
  .trip-plan h2,
  .trip-map h2 {
    font-size: 24px;
    color: #111;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .trip-plan ul {
    list-style-type: none;
    padding: 0;
  }
  
  .trip-plan li {
    background: #f8f9fa;
    padding: 15px;
    margin-bottom: 10px;
    border-radius: 10px;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s ease;
  }
  
  .trip-plan li:hover {
    background-color: #eef5ff;
  }
  
  .day-summary {
    cursor: pointer;
    font-weight: 600;
    color: #333;
    position: relative;
  }
  
  .day-summary::before {
    content: '➤';
    margin-right: 10px;
    color: #00aaff;
    transition: transform 0.3s ease;
  }
  
  .day-summary:hover::before {
    transform: rotate(90deg);
  }
  
  .day-details {
    margin-top: 15px;
    padding: 15px;
    background: #fdfdfd;
    border-left: 4px solid #00aaff;
    border-radius: 5px;
    box-shadow: 0 3px 15px rgba(0, 0, 0, 0.1);
  }
  
  h3 {
    color: #555;
  }
  
  ul {
    padding-left: 20px;
  }
  
  ul li {
    margin-bottom: 8px;
  }
  
  /* Map Section */
  .map-placeholder {
    height: 300px;
    background: #eef2f3;
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  }
  
  iframe {
    border-radius: 10px;
  }
  
  /* Responsive Styles */
  @media screen and (max-width: 768px) {
    .details-sections {
      flex-direction: column;
      gap: 20px;
    }
  
    .trip-plan,
    .trip-map {
      width: 100%;
    }
  }
 /* Default Light Mode Styles */
.trip-details-page {
    background-color: white;
    color: black;
    font-family: Arial, sans-serif;
    transition: background-color 0.3s ease, color 0.3s ease;
  }
  
  .dark-mode {
    background-color: #121212;  /* Dark background */
    color: white;  /* Light text color */
  }
  
  .dark-mode .media-gallery h2,
  .dark-mode .details-sections h2 {
    color: white;
  }
  
  .dark-mode .day-summary {
    color: white;
  }
  
  .dark-mode .day-details {
    background-color: #333; /* Darker background for day details */
    color: white;
  }
  
  .dark-mode .trip-map iframe {
    border: 2px solid #555; /* Dark border for the map iframe */
  }
  
  /* Button for dark mode toggle */
  .dark-mode-toggle {
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
    margin-bottom: 20px;
    transition: background-color 0.3s ease;
  }
  
  .dark-mode-toggle:hover {
    background-color: #0056b3;
  }
  
 
  
  .trip-map iframe {
    border: 2px solid #ccc;
  }
  
  /* Additional styling to adjust elements for better visibility in dark mode */
